"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { Book, Headphones, Heart, Crown, Search, MoreHorizontal, ExternalLink, Trash2 } from "lucide-react"
import { deleteResource, fetchResources, type ResourceRecord } from "@/lib/api"
import { useToast } from "@/components/ui/use-toast"

type ResourceType = "all" | "novel" | "audio" | "comic"

const typeConfig = {
  novel: { icon: Book, label: "小说", className: "bg-primary/10 text-primary" },
  audio: { icon: Headphones, label: "音频", className: "bg-chart-2/10 text-chart-2" },
  comic: { icon: Heart, label: "漫画", className: "bg-chart-5/10 text-chart-5" },
}

const filterLabels: Record<ResourceType, string> = {
  all: "全部",
  novel: "小说",
  audio: "音频",
  comic: "漫画",
}

export function ResourceList() {
  const [filter, setFilter] = useState<ResourceType>("all")
  const [search, setSearch] = useState("")
  const [resources, setResources] = useState<ResourceRecord[]>([])
  const [loading, setLoading] = useState(false)
  const { toast } = useToast()

  useEffect(() => {
    let ignore = false
    const load = async () => {
      try {
        setLoading(true)
        const data = await fetchResources(filter)
        if (!ignore) {
          setResources(data)
        }
      } catch (error) {
        const message = error instanceof Error ? error.message : "加载失败"
        toast({
          title: "读取资源失败",
          description: message,
          variant: "destructive",
        })
      } finally {
        if (!ignore) {
          setLoading(false)
        }
      }
    }
    load()
    return () => {
      ignore = true
    }
  }, [filter, toast])

  const filteredResources = resources.filter((r) => {
    const matchesType = filter === "all" || r.type === filter
    const matchesSearch = r.title.toLowerCase().includes(search.toLowerCase())
    return matchesType && matchesSearch
  })

  const removeResource = async (id: string) => {
    try {
      await deleteResource(id)
      setResources((prev) => prev.filter((item) => item.id !== id))
      toast({ title: "已删除资源" })
    } catch (error) {
      const message = error instanceof Error ? error.message : "删除失败"
      toast({
        title: "删除失败",
        description: message,
        variant: "destructive",
      })
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-card-foreground">资源列表</CardTitle>
        <div className="flex flex-col sm:flex-row gap-3 mt-4">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="搜索资源..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-9"
            />
          </div>
          <div className="flex gap-1">
            {(["all", "novel", "audio", "comic"] as const).map((type) => (
              <Button
                key={type}
                variant={filter === type ? "default" : "outline"}
                size="sm"
                onClick={() => setFilter(type)}
              >
                {filterLabels[type]}
              </Button>
            ))}
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="rounded-lg border border-border overflow-hidden">
          <Table>
            <TableHeader>
              <TableRow className="bg-muted/50">
                <TableHead>标题</TableHead>
                <TableHead>类型</TableHead>
                <TableHead>状态</TableHead>
                <TableHead>创建日期</TableHead>
                <TableHead className="w-12"></TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredResources.map((resource) => {
                const config = typeConfig[resource.type]
                const Icon = config.icon
                return (
                  <TableRow key={resource.id}>
                    <TableCell className="font-medium">{resource.title}</TableCell>
                    <TableCell>
                      <Badge variant="secondary" className={config.className}>
                        <Icon className="h-3 w-3 mr-1" />
                        {config.label}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      {resource.isVip && (
                        <Badge className="bg-warning text-warning-foreground">
                          <Crown className="h-3 w-3 mr-1" />
                          VIP
                        </Badge>
                      )}
                    </TableCell>
                    <TableCell className="text-muted-foreground text-sm">{resource.createdAt}</TableCell>
                    <TableCell>
                      <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                          <Button variant="ghost" size="icon" className="h-8 w-8">
                            <MoreHorizontal className="h-4 w-4" />
                          </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent align="end">
                          <DropdownMenuItem onClick={() => window.open(resource.link, "_blank")}>
                            <ExternalLink className="h-4 w-4 mr-2" />
                            打开链接
                          </DropdownMenuItem>
                          <DropdownMenuItem onClick={() => removeResource(resource.id)} className="text-destructive">
                            <Trash2 className="h-4 w-4 mr-2" />
                            删除
                          </DropdownMenuItem>
                        </DropdownMenuContent>
                      </DropdownMenu>
                    </TableCell>
                  </TableRow>
                )
              })}
              {loading && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8 text-muted-foreground">
                    正在加载数据...
                  </TableCell>
                </TableRow>
              )}
              {!loading && filteredResources.length === 0 && (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8 text-muted-foreground">
                    未找到资源
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  )
}
